import { ReactNode } from "react"
import CssBaseline from "@mui/material/CssBaseline"
import { ThemeProvider as MUIThemeProvider } from "@mui/material/styles"
import { theme as RPTheme } from "@rp/react/theme"
import { RPThemeProps } from "@rp/react/theme"

type ThemeProviderProps = {
  children: ReactNode
  theme?: RPThemeProps
}

export const ThemeProvider = ({ children, theme = RPTheme }: ThemeProviderProps) => (
  <MUIThemeProvider theme={theme}>
    <CssBaseline />
    {children}
  </MUIThemeProvider>
)
